<template>
    <div id="order-form">
        <!-- 报表 menu start  :class="{'active':index === menuSelect}"-->
        <ul class="clearfix menu-content">
            <router-link
            v-for="(item, index) in formType"
            :key="item.index"
            tag="li"
            class="left"
            :to="item.path"
            active-class="active"
            >
              <span>{{ item.name }}</span>
            </router-link>
        </ul>
        <!-- 报表 menu end -->

        <!-- order content start -->
        <div class="order-content">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
        <!-- order content end -->
    </div>
</template>

<script>
export default {
  data() {
    return {
      formType: [
        {
          name: "全部",
          path: "allform"
        },
        {
          name: "未支付",
          path: "notpayform"
        },
        {
          name: "已支付",
          path: "payform"
        },
        {
          name: "挂账",
          path: "iouform"
        },
        {
          name: "已反结",
          path: "backform"
        },
        {
          name: "退款成功",
          path: "refundsuccessform"
        },
        {
          name: "退款失败",
          path: "refundfailform"
        },
        {
          name: "已关闭",
          path: "closeform"
        }
      ],
      menuSelect: 0
    };
  },
  methods: {

  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.menu-content {
  @include fc(14px, #333);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
  margin-bottom: 10px;
  min-width: 1100px;
  li {
    height: 60px;
    line-height: 60px;
    margin-right: 80px;    
    padding: 0 5px;
    cursor: pointer;
  }

  li:first-child {
    margin-left: 21px;
  }

  li.active {
    border-bottom: 4px solid #4877E7;
  }
}

</style>


